.ygbh-footer .footer-l{ float: left; }
.ygbh-footer .footer-r{ float: right; }
.ygbh-footer .footer-link{ color: #333; text-decoration: none;}
.ygbh-footer .footer-link:hover{ color: #409eff; }
</style>