<template functional>
<div class="bh-divider" :class="[`bh-divider-${props.direction}`]">
	<slot></slot>
</div>
</template>

<script>
export default {
	name: 'bh-divider',
	props: {
		direction: {
			type: String,
			default: 'horizontal',
			validator(val) {
				return ['horizontal', 'vertical'].indexOf(val) !== -1;
			}
		},
	},
}
</script>
