<template>
<div class="bh-skeleton">
	<div class="bh-skeleton__item is-first"></div>
	<div class="bh-skeleton__item" v-for='item in rowsLen' :key='item'></div>
	<div class="bh-skeleton__item is-last"></div>
</div>
</template>

<script>
export default {
	name: 'bh-skeleton',
	props: {
		rows: {
			type: Number,
			default: 4,
		},
	},
	data() {
		return {
			visible: true,
		}
	},
	methods: {
	},
	computed: {
		rowsLen () {
			return this.rows - 2;
		}
	},
}
</script>
