<template>
<label class="bh-radio">
	<span class="bh-radio__input">
		<input type="radio" :value="label" v-model="model" name="" @change="handleChange">
	</span>
	<span class="bh-radio__label">
		<slot></slot>
		<template v-if="!$slots.default">{{label}}</template>
	</span>
</label>
</template>

<script>
export default {
	name: 'bh-radio',
	props: {
		value: {},
		label: {},
	},
	computed: {
		model: {
			get() {
				return this.value
			},
			set(val) {
				this.$emit('input', val);
			},
		},
	},
	methods: {
		handleChange() {
			this.$nextTick(() => {
				this.$emit('change', this.model);
			});
		},
	},
}
</script>
