<template>
<label class="bh-checkbox">
	<span class="bh-checkbox__inner">
		<input type="checkbox" :value="label" v-model="model" name="" @change="handleChange" class="bh-checkbox__inputcheckbox">
	</span>
	<span class="bh-radio__label">
		<slot></slot>
		<template v-if="!$slots.default">{{label}}</template>
	</span>
</label>
</template>

<script>
import Emitter from '../../../../src/mixins/emitter';
export default {
	name: 'bh-checkbox',
	mixins: [Emitter],
	props: {
		value: {},
		label: {},
	},
	data() {
		return {
			//checkboxgroupParent: null,
		};
	},
	computed: {
		model: {
			get() {
				if(this.isGroup) {
					if(this.$parent.value.includes(this.label)) {
						return true;
					}
					return false;
				}else {
					return this.value	
				}
			},
			set(val) {
				this.$emit('input', val);
			},
		},
		// 
		isGroup() {
			if (this.$parent.$options.name == 'bh-checkbox-group') {
				//this.checkboxgroupParent = this.$parent;
				return true;
			}

			return false;
		},
	},
	methods: {
		handleChange(ev) {
			this.$nextTick(() => {
				if(this.isGroup) {
					this.handleCheckboxClick(ev);
				}else {
					this.$emit('change', this.model);
				}
			});
		},
		handleCheckboxClick(ev) {
			this.$dispatch('BHCheckboxGroup', 'handleCheckboxClick', [this.label, ev.target.checked]);
		},
	},
}
</script>
