<template>
<div class="bh-collapse">
	<slot></slot>
</div>
</template>

<script>
export default {
	name: 'bh-collapse',
	props: {
		type: {
			type: String,
			default: 'info',
		},
		title: {
			type: String,
			default: '',
		},
		closable: {
			type: Boolean,
			default: true,
		},
	},
	data() {
		return {
			visible: false,
		}
	},
	methods: {
		close() {
			this.visible = false;
			this.$emit('close');
		},
	},
	computed: {
		typeClass() {
			return `bh-alert-${ this.type }`;
		},
	},
}
</script>
