<template>
<form class="bh-form">
	<slot></slot>
</form>
</template>

<script>
export default {
	name: 'bh-form',
	provide() {
		return {
			bhForm: this,
		};
    },
	props: {
		labelWidth: String,
	},
	data() {
		return {
			visible: true,
		}
	},
	methods: {
		close() {
			this.visible = false;
			this.$emit('close');
		},
	},
	computed: {
		typeClass() {
			return `bh-alert-${ this.type }`;
		},
	},
}
</script>
