<template>
<div class="bh-slider"
	@mousedown="onButtonDown"
    @touchstart="onButtonDown">
	<div class="bh-slider__runway">
		<div class="bh-slider__bar" :style="{'width': formatValue + '%'}"></div>
		<div class="bh-slider__button-wrapper" :style="{'left': formatValue + '%'}">
			<div class="bh-tooltip bh-slider__button" :title='formatValue'></div>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'bh-slider',
	props: {
		value: {
			type: Number,
			default: 0
		},
	},
	data() {
		return {
			dragging: false,
			startPosition: 0,
			newPosition: 0,
			startX: 0,
			startY: 0,
			currentX: 0,
		}
	},
	computed: {
		currentPosition() {
			return this.value;
		},
		formatValue() {
			return this.value
		},
    },
	methods: {
		onButtonDown(event) {
			event.preventDefault();
			this.onDragStart(event);
			window.addEventListener('mousemove', this.onDragging);
			window.addEventListener('touchmove', this.onDragging);
			window.addEventListener('mouseup', this.onDragEnd);
			window.addEventListener('touchend', this.onDragEnd);
			window.addEventListener('contextmenu', this.onDragEnd);
		},
		onDragStart(event) {
			this.dragging = true;
			console.log('onDragStart', event.type);
			if (event.type === 'touchstart') {
				event.clientY = event.touches[0].clientY;
				event.clientX = event.touches[0].clientX;
			}
			if (this.vertical) {
				this.startY = event.clientY;
			} else {
				this.startX = event.clientX;
			}
			this.startPosition = parseFloat(this.currentPosition);
			this.newPosition = this.startPosition;
		},
		onDragging(event) {
			if (this.dragging) {
				let diff = 0;
				if (this.vertical) {
					this.currentY = event.clientY;
					diff = (this.startY - this.currentY);
				} else {
					this.currentX = event.clientX;
					diff = (this.currentX - this.startX);
				}
				this.newPosition = this.startPosition + diff;
				this.setPosition(this.newPosition);
			}
		},
		onDragEnd(event) {
			if(this.dragging) {
				console.log('onDragEnd', event.type);
				window.removeEventListener('mousemove', this.onDragging);
				window.removeEventListener('touchmove', this.onDragging);
				window.removeEventListener('mouseup', this.onDragEnd);
				window.removeEventListener('touchend', this.onDragEnd);
				window.removeEventListener('contextmenu', this.onDragEnd);
			}
		},
		setPosition(newPosition) {
			console.log('setPosition', newPosition, this.$parent.sliderSize);
			let value = newPosition;
			this.$emit('input', value);
		},
	},
}
</script>
