<template>
<div class="bh-image">
	<img :src="src" class="bh-image__inner">
</div>
</template>

<script>
export default {
	name: 'bh-image',
	props: {
		src: {
			type: String,
			default: '',
		},
	},
	data() {
		return {
			visible: true,
		}
	},
	methods: {
		close() {
			this.visible = false;
			this.$emit('close');
		},
	},
	computed: {
		typeClass() {
			return `bh-alert-${ this.type }`;
		},
	},
}
</script>
