<template>
<li class="bh-timeline-item">
	<div class="bh-timeline-item__tail"></div>
	<div class="bh-timeline-item__node"></div>
	<div class="bh-timeline-item__wrapper">
		<div class="bh-timeline-item__content">
			<slot></slot>
		</div>
		<div class="bh-timeline-item__timestamp">
			{{timestamp}}
		</div>
	</div>
</li>
</template>

<script>
export default {
	name: 'bh-timeline-item',
	props: {
		timestamp: {
			type: String,
			default: '',
		},
	},
	data() {
		return {
			visible: true,
		}
	},
	methods: {
		close() {
			this.visible = false;
			this.$emit('close');
		},
	},
	computed: {
		typeClass() {
			return `bh-alert-${ this.type }`;
		},
	},
}
</script>
